
$gradient: linear-gradient(
  to right,
  rgba(11, 11, 11, 0.5),
rgba(11, 11, 11, 0.5)
);
$gradient-overlay: linear-gradient(
   to top,
    rgba(13, 13, 13, 0.19),
    rgba(14, 14, 14, 0.8)
);


.hero-section {
  position: relative;
  height: 364px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  background-color: $dark-color;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  background-image: $gradient, url("../images/mobile/hero.png");

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: $gradient, url("../images/mobile/hero@2x.png");
  }

  @media screen and (min-width: 768px) {
     
    background-image: $gradient, url("../images/tablet/hero.png");

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: $gradient, url("../images/tablet/hero@2x.png");
    }
  }

  @media screen and (min-width: 1280px) {
    height: 446px;
    max-width: 1600px;
    // padding-top: 200px;
    // padding-bottom: 200px;
    background-image: $gradient, url("../images/desktop/hero.png");

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: $gradient, url("../images/desktop/hero@2x.png");
    }
  }
}

 .logo {
    display:inline-block;
   align-items: flex-start;
  }
  
  .hero-title {
   
    display: inline-block;
    color: $accent-color;
    font-size: 18px;
    line-height: 1.22;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    margin-top: -15px;
   
    @media screen and (min-width: 768px) {
    position: absolute;
     top: 0;
   right: 27%;
        font-size: 30px;
        max-width: 370px;
         margin-top: 40px;
         max-height: 74px;
          padding-right: 0;
   padding-left: 0;
    }

    @media screen and (min-width: 1200px) {
      font-size: 50px;
     margin-top: 40px;
      max-width: 600px;
      max-height: 122px;
     
      
    }
  }

.search-form{
    text-align: center;
    
     @media screen and (min-width: 768px) {
         display: flex;
         justify-content: center;
          position: absolute;
          top: 30%;
    }
 @media screen and (min-width: 1280px) {
         
          top: 45%;
          right: 27%;

    }
}
.complete-input{
     position: relative;
}
.search-input {

    width: 270px;
    height: 40px;
   margin-top: 25px;
    padding: 10px 30px;
   background-color: $secondary-color-02;
    backdrop-filter: blur(20px);
    border-radius: 20px;
    border: none;
     @media screen and (min-width: 768px) {
         display: flex;
         justify-content: center;
 margin-left: 30px;
    }
    @media screen and (min-width: 1280px) {
          margin-top: 13px;
         
    }

&::placeholder {
    
      color: $secondary-color;
      font-size: 16px;
      line-height: 1.25;

    }

  }

  .search-icon{
  position: absolute;
  //top: 0;
  right: 30px;
  width: 12px;
  height: 12px;
  fill: $secondary-color;
  transform: translate(-200%, -200%);

  @media screen and (min-width: 480px) {
      right: 90px;
    }

   @media screen and (min-width: 768px) {
       right: 30px;
        transform: translate(-50%, -200%);
    }

}

.overlay {
  position: absolute;
  width: 320px;
  height: 102px;
  left: 0px;
  top: 262px;
  background: $gradient-overlay;

  @media screen and (min-width: 768px) {
    width: 768px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    top: 344px;
    
  }
}